.Navbar {
  background-color: rgb(68, 65, 87);
}

a.link {
  color: rgba(245, 245, 245, 0.68);
}

a.link.active {
  color: white;
}

li:hover {
  background-color: rgb(45, 43, 58);
}

li a:hover {
  color: white;
}
.side-nav {
  background-color: rgb(68, 65, 87);
}

.logo {
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
  padding: 0.5rem;
  display: block;
  max-width: 200px;
  max-height: 60px;
  height: auto;
  width: auto;
}

@media only screen and (max-width: 991px) {
  .logo {
    -webkit-filter: brightness(0) invert(1);
            filter: brightness(0) invert(1);
    padding: 0.5rem;
    display: block;
    max-width: 250px;
    max-height: 50px;
    height: auto;
    width: auto;
  }
}
@media only screen and (max-width: 600px) {
  .logo {
    -webkit-filter: brightness(0) invert(1);
            filter: brightness(0) invert(1);
    padding: 0.6rem;
    display: block;
    max-width: 250px;
    max-height: 50px;
    height: auto;
    width: auto;
  }
}



